.country-filter__list {
  @include list-reset;
}

.country-filter__group {
  display: none;

  &--active {
    display: block;
  }
}

.country-filter__title {
  display: none;
}

.country-filter__link {
  font-weight: 400;
  color: #444444;
}

@media (min-width: $tablet-width) {
  .country-filter__link {
    font-size: 20px;
    font-weight: 400;
    line-height: 30px;
  }
}

@media (min-width: $desktop-width) {
  .country-filter {
    display: flex;
    flex-wrap: wrap;
  }

  .country-filter__group {
    display: block;
    margin-bottom: 69px;
    width: 252px;

    &:nth-child(n + 10) {
      margin-bottom: 104px;
    }

    &:nth-child(n + 16) {
      margin-bottom: 64px;
    }

    &:nth-child(n + 21) {
      margin-bottom: 35px;
    }
  }

  .country-filter__title {
    display: block;
    margin-bottom: 48px;

    font-size: 60px;
    font-weight: 700;
    line-height: 24px;
    text-transform: uppercase;
    color: #192144;
  }
}
