.page-footer {
  position: relative;
  z-index: 3;

  padding-top: 38px;
  padding-bottom: 10px;

  background-color: $white;
  border-radius: 30px 30px 0 0;
}

.page-footer__logo {
  display: block;
  margin: 0 auto;
  margin-bottom: 41px;
  width: 96px;
  height: 15px;
}

.page-footer__social {
  margin: 0 auto;
  margin-bottom: 38px;
  width: 135px;
}

.page-footer__copyright {
  padding-top: 13px;

  border-top: 1px solid rgba(#000000, 0.05);
}

@media (min-width: $tablet-width) {
  .page-footer {
    padding-top: 47px;
    padding-bottom: 47px;

    border-radius: 60px 60px 0 0;
  }

  .page-footer__wrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  .page-footer__logo {
    margin: 0;
    margin-right: 90px;
    width: 164px;
    height: 42px;
  }

  .page-footer__social {
    margin: 0;
    width: 180px;
  }

  .page-footer__copyright {
    margin-left: auto;
    padding-top: 6px;
    width: 155px;

    border: none;
  }
}

@media (min-width: $desktop-width) {
  .page-footer__logo {
    margin-right: auto;
    width: 198px;
    height: 52px;
  }

  .page-footer__social {
    width: 226px;
  }
}
